  color: #fff;
}

.btn {
  flex-shrink: 0;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 32px;
  font-size: 32px;
  color: #3BEEB7;
  background-color: transparent;
  padding: 0;
  border: none;
  text-transform: uppercase;
  font-family: Rubik, sans-serif;
  transition: 0.25s color;
}
@media screen and (max-width: 767px) {
  .btn {
    font-size: 24px;
    gap: 20px;
  }
}
@media screen and (max-width: 359px) {
  .btn {
    font-size: 22px;
  }
}
.btn svg {
  display: block;
  max-width: 90px;
  fill: #3BEEB7;
  transition: 0.25s fill;
}
.btn:hover {
  color: #35d4a4;
}
.btn:hover svg {
  fill: #35d4a4;
}

.input {
  display: block;
  width: 100%;
  font-size: 32px;
  color: #3BEEB7;
  background: transparent;
  border: none;
  border-radius: 40px;
  border-bottom: 2px solid #ddd;
  text-align: center;
  padding: 20px 20px;
  font-family: Rubik;
}
