  flex-direction: column;
  gap: 30px;
  font-size: 32px;
  color: #3BEEB7;
  border: none;
  background-color: transparent;
  text-transform: uppercase;
  padding: 0;
  padding-bottom: 34px;
  font-family: Rubik, sans-serif;
}
@media screen and (max-width: 1023px) {
  .video__btn {
    padding-bottom: 0;
